.l-files-list {
  margin-bottom: 35px; }
  .l-files-list__row {
    display: flex;
    flex-wrap: wrap; }

.b-file {
  margin-bottom: 15px; }
  .b-file__name {
    color: #202020;
    text-align: center; }
    .b-file__name:hover, .b-file__name:focus, .b-file__name:active {
      color: #202020;
      text-decoration: none; }
  .b-file__image {
    display: block;
    background: #f7f7f8;
    height: 190px;
    line-height: 190px;
    text-align: center; }
  .b-file__name {
    display: block;
    margin: 5px 0;
    font-weight: 700; }
  .b-file .b-file__ext {
    display: block;
    font-size: 13px;
    text-align: center; }
